.container_videopage{
    height: calc(100vh-3.4rem);
    overflow-y: auto;
    background-color: rgb(255, 255, 255);
    color: black;
}
.container2_videopage{
    margin: 1.5rem 4.5rem;
    display: flex;
}
.video_display_screen_videopage{
    width: calc(100vw-4.5rem-35vw);
    padding: 1rem;
}
.video_showvideo_videopage{
    padding: 0.1rem;
    height: 70vh;
    width: 100%;
    background-color: black;
    border: 1px solid rgba(37,37,37,0.2);
}
.video_btns_title_videoapge_cont{
    border-bottom: 3px solid rgba(64, 64, 63, 0.175);
    flex-grow: 1;
}
.video_title_videopage{
    font-size: x-large;
    margin: 0;
    padding: 1rem 0.8rem;
}

.views_date_btns_videopage{
    display: flex;
    margin: 0.5rem 1rem;
    width: calc(100vw-6.5rem-35vw);
}
.views_videopage{
    display: flex;
    font-size: 0.8rem;
    flex-grow: 1;
    align-items: center;
}
.channel_details_videopage{
    border-bottom: 3px solid rgba(54, 53, 52, 0.175);
    display: flex;
    text-decoration: none;
    color: black;
    font-weight: bolder;
}
.channel_logo_videopage{
    display: flex;
    border: 1px solid rgba(0,00,00.19);
    width: 4rem;
    height: 4rem;
    margin: 0.5rem;
    border-radius: 100%;
    background-color: rgba(0,0,0,0.667);
}
.channel_logo_videopage p{
    color: rgb(255, 255, 255);
    margin: auto;
    font-size: xx-large;
}
.channel_name_videopage{
    font-weight: bolder;
    margin: auto 1rem;
    font-size: larger;
}
.comments_videopage{
    width: 100%;
    padding-inline-start: 0.5rem;
}
.morevideobar{
    width: calc(100vw-4.5rem-70vw);
    padding: 0.1rem;
}